.btn {
  padding: 16px 40px;
  transition: background-color 0.5s;
  border: 0;
  border-radius: 6px;
  background: $color-brand;
  color: #fff;
  font-size: $font-size-base;
  font-weight: bold;
  cursor: pointer;

  &:hover {
    background: #fe6a94;
  }

  &_link--ib {
    // composes: btn; // compose === local @extend
    display: inline-block;
    transition: all 0.3s;

    &:hover {
      background-color: #000;
    }
  }
}
